import React, { useState,useEffect} from "react";
import Navbar from "./components/Navbar";
import Search from "./components/Search";
import Results from "./components/Results";
import "./App.css";
import Notfound from "./components/Notfound";

function App() {
  const [search, usesearch] = useState("spiderman");
  const [results, useresults] = useState([]);
  const [error, useerror] = useState(false);
  const API_KEY = "8a2208e5a0ce32e0e5044f64bc78c38d";

  const searchfunc = (e) => {
    if (search) {
      const BASE_URL = `https://api.themoviedb.org/3/search/movie?api_key=${API_KEY}`;

      const FETCH_URL = `${BASE_URL}&query=${search}`;
      console.log(FETCH_URL);
      fetch(FETCH_URL, {
        method: "GET",
      })
        .then((response) => response.json())
        .then((json) => {
          useresults(json.results);
          useerror(false);
        });
    } else {
      useerror(true);
    }
  };
  const handlesubmit = (e) => {
    e.preventDefault();
    searchfunc();
  };
  useEffect(() => {
    searchfunc()
    return () => {
    }
  }, [search])

  console.log(`results${results},error${error},search ${search}`);
  return (
    <div className="App">
      <Navbar />
      <Search search={usesearch} handlesubmit={handlesubmit} />
      {!results && <Notfound error="There is no such film  :(" />}
      {search.length < 0 && <Notfound error=" You have not performed any search  :(" />}
      <Results results={results} />
    </div>
  );
}

export default App;
