import React from "react";
// import { Card } from '@material-ui/core'
import broken_image from "../css/download (2).png";
import "../css/results.css";
export default function Results(props) {
  if (props.results) {
    var resultitems = props.results.map((result, id) => {
      const imgpath =
        result.poster_path !== null
          ? `https://image.tmdb.org/t/p/w500/${result.poster_path}`
          : broken_image;
      return (
        <div key={id} className="results__div" >
          <a href={imgpath} target='_blank' style={{textDecoration: 'none'}}>
          <div className='result__align'>
          <img alt={result.title} src={imgpath} className="results__img" />
          <div className="result__info">
            <h4>{result.title}</h4>
            <p>{result.release_date}</p>
          </div>
          </div>
          </a>
        </div>
      );
    });
  }
  return <div className="result__items ">{resultitems}</div>;
}
